.nav {
    padding:  35px 0;
    &__container {
        background-color: var(--color-accent-2);
    }
    &--flex {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1280px) {
            flex-direction: column;
            line-height: 2;
            font-size: 28px;
            text-align: center;
            padding: 0yy;
        }
    }
    &__item {
        color: var(--color-white);
        font-size: 18px;
        padding-right: 65px;
        cursor: pointer;
        @media (max-width: 1280px) {
            padding: 0;
        }
        &:hover {
            transition: 0.3s ease-in-out;
            color: var(--color-accent-1);
        }
    }
    &__link {
        display: flex;
        align-items: center;
        color: var(--color-white);
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 175px;
        height: 40px;
        background-color: var(--color-white);
        color: var(--color-black);
        font-size: 18px;
        &:hover {
            transition: 0.3s ease-in-out;
            background-color: var(--color-accent-3);
        }
    }
    &__blind {
        .blind {
            display: flex;
            align-items: center;
            padding: 0;
            height: 90px;
            &__btn {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 270px;
                height: 40px;
                background-color: var(--color-accent-1);
                color: var(--color-white);
                text-transform: uppercase;
                font-size: 14px;
                &:hover {
                    transition: 0.3s ease-in-out;
                    opacity: 0.9;
                }
            }
            &__text {
                padding-left: 10px;
            }
        }
        @media (min-width: 1280px) {
            display: none;
        }
    }
}