.header {
    left: 0;
    top: 0;
    color: var(--color-black);
    font: var(--medium-regular);
    font-size: 16px;
    background-color: var(--color-white);
    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__flex-end {
        display: flex;
    }
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1280px) {
            display: none;
        }
    }
    &__item {
        display: flex;
        align-items: center;
        padding: 0 28px;
        height: 90px;
    }
    &__blind {
        display: flex;
    align-items: center;
    padding: 0;
    height: 90px;
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 270px;
        height: 40px;
        background-color: var(--color-accent-1);
        color: var(--color-white);
        text-transform: uppercase;
        font-size: 14px;
        &:hover {
            transition: 0.3s ease-in-out;
            opacity: 0.9;
        }
    }
    &__text {
        padding-left: 10px;
    }
        @media (max-width: 1280px) {
            display: none; 
        }
    }

}
.blind {
    
}
.tel {
    display: flex;
    align-items: center;
    padding: 0 30px;
    height: 90px;
    &__number {
        color: var(--color-black);
    }
}
.logo {
    display: flex;
    align-items: center;
    height: 90px;
    min-width: 210px;
    padding: 0 30px;
}

.menu {
    display: flex;
    @media (max-width: 1280px) {
        flex-direction: column;
        z-index: 2;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 15px;
        background: var(--color-accent-1);
        transform: translateX(-100%);
        transition: transform 0.5s;
        &.active {
            transform: translateX(0);
            & span:nth-of-type(1) {
                display: none;
            }
            & span:nth-of-type(2) {
                top: 50%;
                transform: translate(-50%, 0%) rotate(45deg);
            }
            & span:nth-of-type(3) {
                top: 50%;
                transform: translate(-50%, 0%) rotate(-45deg);
            }
        }
        & li {
            list-style-type: none;
        }
    }

    &-btn {
        min-width: 30px;
        height: 30px;
        position: relative;
        z-index: 3;
        overflow: hidden;
        @media (min-width: 1280px) {
            display: none;
        }
        & span {
            width: 30px;
            height: 2px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: var(--color-accent-2);
            transition: all 0.5s;
            &:nth-of-type(2) {
                top: calc(50% - 5px);
            }
            &:nth-of-type(3) {
                top: calc(50% + 5px);
            }
        }
    }
}
